<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                活动通知
                <img class="search" src="../../assets/technology/icon_search@2x.png" alt="" @click="goSearch">
            </p>
        </section>
        <section class="middle">
            <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-6 top-tab-bar">
            <!--<md-tab-bar-->
                <!--v-model="current"-->
                <!--:items="items"-->
                <!--:has-ink="false"-->
                <!--@change="onTabChange"-->
            <!--&gt;</md-tab-bar>-->
                <div class="none" v-if="!list.length"  :style="{height: Height}">暂无数据</div>
            <md-scroll-view :dragable="false" :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                <div
                            v-for="(i, key) in list"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <img :src="i.picUrl" alt="">
                        <div class="article">
                            <p>
                                <span style="display: inline-flex;align-items: center;position: absolute;top: 4px;">
                                    <label class="icon" v-if="i.type === 1"></label>
                                    <label class="line" v-if="i.type === 1"></label>
                                </span>
                                <label :class="{'padding': i.type === 1}" style="line-height: 22px">{{ i.title }}</label>
                            </p>
                            <span><img src="@/assets/home/icon_time@2x.png" alt="">{{ i.startTime.substr(0,10) }}-{{ i.endTime.substr(0,10)}}</span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            </div>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/activity'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, Icon, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [Icon.name]: Icon,
    [Toast.name]: Toast
  },
  data () {
    return {
      // 当前选中tab
      current: 1,
      // tab项
      items: [{ name: 1, label: '近期活动' }, { name: 2, label: '平台活动' }],
      list: [],
      isFinished: false,
      Height: '900px',
      searchParams: {
        pageSize: 10,
        pageNumber: 1
      }
    }
  },
  mounted () {
    this.Height = window.innerHeight - 44 + 'px'
    this.getList()
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.list = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.searchParams.pageNumber = 1
      this.searchParams.type = item.name
      this.getList()
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/activity/detail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.list = this.list.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    addItems () {
      this.list += 5
      // 如果把autoReflow设置为false, 需调用reflowScroller
      this.$refs.scrollView.reflowScroller()
    },
    // 回退
    goBack () {
      this.$router.push('/home')
    },
    // 跳转关键字检索页
    goSearch () {
      this.$router.push('/search/index')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .middle
        position fixed
        top 88px
        width 100%
    .md-swiper
        background #FFF
    .md-swiper-item
        padding 20px
        box-sizing border-box
        font-size 24px
        line-height 1.5
        color #666
    .scroll-view-item
        padding 30px 10px
        border-bottom 2px solid rgba(153,153,153,0.2)
        text-align center
        font-size 28px
        font-family DINAlternate-Bold
        height 204px
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                    position relative
                span
                    display flex
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
                .icon
                    display inline-block
                    background url("../../assets/activity/setTop@2x.png") no-repeat
                    background-size 100% 100%
                    width 42px
                    height 18px
                .line
                    height 25px
                    display inline-block
                    margin 0 0.15rem
                    color #999
                    border-left 2px solid #999
        .md-scroll-view-more
            justify-content center
    /deep/.md-tab-bar .md-tab-bar-list
        border-top 0!important
    .md-scroll-view
        padding 20px
    .padding
        padding-left 80px
</style>
